import { AuthProvider } from './../../providers/auth/auth';
import { Component } from '@angular/core';
import { IonicPage, NavController } from 'ionic-angular';
import { contentHierarchy } from '../../app/app.config';
import { Observable } from 'rxjs';
import { AngularFirestore } from 'angularfire2/firestore';
import { Collections, Content, User } from '../../app/app.models';
import { map } from 'rxjs/operators';

@IonicPage()
@Component({
  selector: 'page-mi-curso',
  templateUrl: 'mi-curso.html',
})
export class MiCursoPage {

  private mainContent: string = [...contentHierarchy].shift();
  public content$: Observable<Content[]>;

  constructor(
    private afs: AngularFirestore,
    public auth: AuthProvider,
    public navCtrl: NavController
  ) {}

  ngOnInit() {
    this.auth.user$.subscribe(user => user && !this.content$ ? this.loadContent(user) : null);
  }

  loadContent(user: User) {
    this.content$ = this.afs
      .collection<Content>(Collections.CONTENT, ref =>
        ref.where('type', '==', this.mainContent)
      )
      .valueChanges()
      .pipe(
        map(content =>
          content
            .filter(c => !c.name.includes('Temprano'))
            .filter(c => user.isPremium2019 ? true : !c.ignoreOnSmartCalendar)
            .filter(c => user.isPremium2019 ? true : c.liberadoInPrograma)
        )
      );
  }

  public isBlur(name: string): boolean {
    if (!this.auth.user) return true;
    if (!name) return true;
    if (this.auth.isAdmin) return false;
    if (this.auth.isZamna360_2019) return false;
    if (this.auth.isPremium2019) return false;
    if (name.toLowerCase().includes('temprano')) {
      return !this.auth.isTemprano;
    }
    return false;
  }

  public isLiberado(content: Content): boolean {
    if (!this.auth.user) return false;
    if (this.auth.user.isAdmin) return true;
    if (this.auth.user.isPremium2019) return true;
    return content.liberadoInPrograma;
  }

}



